.banner {
  margin-top: 10vh;
  height: 60vh;
  color: white;
  object-fit: contain;
  position: relative;
}

.banner__content {
  margin-left: 30px;
  padding-top: 30px;
}

.banner__title {
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}

.banner__description {
  width: 45rem;
  line-height: 1.3;
  padding: 1rem 0;
  font-size: 1rem;
  max-width: 360px;
  height: 80px;
}
.banner__button {
  cursor: pointer;
  color: #fff;
  outline: none;
  border: none;
  font-size: 1.2rem;
  font-weight: 700;
  border-radius: 0.2vh;
  padding: 1rem 3rem;
  margin-right: 2rem;
  background-color: rgba(51, 51, 51, 0.5);
} 

.play {
  color: #000;
  background-color: #fff;
}

.banner__button:hover {
  color: #000;
  background-color: #c6c6c6;
  transition: all 0.2s;
}

.banner__fadeBottom {
  height: 40vh;
  width: 100%;
  position: absolute;
  bottom: 0;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 37, 0.91),
    #111
  );
}
